import { useEffect, useState } from 'react'
import { X, AlertTriangle, Info, AlertCircle, Bell, BellOff } from 'lucide-react'
import { useRealTimeAlerts, useSocket } from '../services/socket'

interface NotificationProps {
  id: string
  type: 'critical' | 'warning' | 'info'
  title: string
  message: string
  timestamp: string
  device?: string
  location?: string
  onDismiss: (id: string) => void
}

const NotificationItem = ({ id, type, title, message, timestamp, device, location, onDismiss }: NotificationProps) => {
  const [isVisible, setIsVisible] = useState(true)

  const getIcon = () => {
    switch (type) {
      case 'critical':
        return <AlertCircle className="w-5 h-5 text-red-500" />
      case 'warning':
        return <AlertTriangle className="w-5 h-5 text-yellow-500" />
      case 'info':
        return <Info className="w-5 h-5 text-blue-500" />
    }
  }

  const getBorderColor = () => {
    switch (type) {
      case 'critical':
        return 'border-l-red-500'
      case 'warning':
        return 'border-l-yellow-500'
      case 'info':
        return 'border-l-blue-500'
    }
  }

  const handleDismiss = () => {
    setIsVisible(false)
    setTimeout(() => onDismiss(id), 300)
  }

  if (!isVisible) return null

  return (
    <div className={`
      transform transition-all duration-300 ease-in-out
      ${isVisible ? 'translate-x-0 opacity-100' : 'translate-x-full opacity-0'}
      bg-white dark:bg-gray-800 border-l-4 ${getBorderColor()} shadow-lg rounded-lg p-4 mb-3
      max-w-sm w-full
    `}>
      <div className="flex items-start justify-between">
        <div className="flex items-start space-x-3 flex-1">
          {getIcon()}
          <div className="flex-1 min-w-0">
            <h3 className="text-sm font-semibold text-gray-900 dark:text-white">
              {title}
            </h3>
            <p className="text-sm text-gray-600 dark:text-gray-300 mt-1">
              {message}
            </p>
            {(device || location) && (
              <div className="flex items-center space-x-4 mt-2 text-xs text-gray-500 dark:text-gray-400">
                {device && <span>Device: {device}</span>}
                {location && <span>Location: {location}</span>}
              </div>
            )}
            <p className="text-xs text-gray-400 dark:text-gray-500 mt-2">
              {new Date(timestamp).toLocaleTimeString()}
            </p>
          </div>
        </div>
        <button
          onClick={handleDismiss}
          className="ml-2 text-gray-400 hover:text-gray-600 dark:hover:text-gray-300"
        >
          <X className="w-4 h-4" />
        </button>
      </div>
    </div>
  )
}

export const RealTimeNotifications = () => {
  const alerts = useRealTimeAlerts()
  const { isConnected } = useSocket()
  const [notifications, setNotifications] = useState<any[]>([])
  const [isEnabled, setIsEnabled] = useState(true)

  useEffect(() => {
    if (alerts.length > 0 && isEnabled) {
      const latestAlert = alerts[0]
      setNotifications(prev => [latestAlert, ...prev.slice(0, 4)]) // Keep max 5 notifications

      // Auto-dismiss after 10 seconds for non-critical alerts
      if (latestAlert.type !== 'critical') {
        setTimeout(() => {
          handleDismiss(latestAlert.id)
        }, 10000)
      }

      // Show browser notification if permission granted
      if (window.Notification && window.Notification.permission === 'granted') {
        new window.Notification(`NOC Alert: ${latestAlert.title}`, {
          body: latestAlert.message,
          icon: '/favicon.ico',
          tag: latestAlert.id
        })
      }
    }
  }, [alerts, isEnabled])

  const handleDismiss = (id: string) => {
    setNotifications(prev => prev.filter(n => n.id !== id))
  }

  const clearAll = () => {
    setNotifications([])
  }

  const toggleNotifications = async () => {
    if (!isEnabled) {
      // Request notification permission
      if (window.Notification && window.Notification.permission === 'default') {
        const permission = await window.Notification.requestPermission()
        if (permission !== 'granted') {
          return
        }
      }
    }
    setIsEnabled(!isEnabled)
  }

  return (
    <>
      {/* Notification Toggle Button */}
      <div className="fixed top-4 right-4 z-50">
        <div className="flex items-center space-x-2">
          {/* Connection Status */}
          <div className={`
            flex items-center space-x-1 px-2 py-1 rounded-full text-xs
            ${isConnected 
              ? 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200' 
              : 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200'
            }
          `}>
            <div className={`w-2 h-2 rounded-full ${isConnected ? 'bg-green-500' : 'bg-red-500'}`} />
            <span>{isConnected ? 'Live' : 'Offline'}</span>
          </div>

          {/* Notification Toggle */}
          <button
            onClick={toggleNotifications}
            className={`
              p-2 rounded-full transition-colors
              ${isEnabled 
                ? 'bg-blue-100 text-blue-600 hover:bg-blue-200 dark:bg-blue-900 dark:text-blue-200' 
                : 'bg-gray-100 text-gray-400 hover:bg-gray-200 dark:bg-gray-800 dark:text-gray-500'
              }
            `}
            title={isEnabled ? 'Disable notifications' : 'Enable notifications'}
          >
            {isEnabled ? <Bell className="w-4 h-4" /> : <BellOff className="w-4 h-4" />}
          </button>

          {/* Clear All Button */}
          {notifications.length > 0 && (
            <button
              onClick={clearAll}
              className="px-3 py-1 text-xs bg-gray-100 hover:bg-gray-200 dark:bg-gray-700 dark:hover:bg-gray-600 text-gray-600 dark:text-gray-300 rounded-full"
            >
              Clear All
            </button>
          )}
        </div>
      </div>

      {/* Notifications Container */}
      <div className="fixed top-16 right-4 z-40 max-w-sm">
        <div className="space-y-2">
          {notifications.map((notification) => (
            <NotificationItem
              key={notification.id}
              {...notification}
              onDismiss={handleDismiss}
            />
          ))}
        </div>
      </div>
    </>
  )
}
