import { useEffect } from 'react'
import { Routes, Route, Navigate } from 'react-router-dom'

// Layout Components
import Layout from '@/components/Layout/Layout'

// Page Components
import Dashboard from '@/pages/Dashboard/Dashboard'
import SystemStatus from '@/pages/SystemStatus/SystemStatus'
import AlertManagement from '@/pages/Alerts/AlertManagement'
import TechnicianWorkload from '@/pages/Technicians/TechnicianWorkload'
import AuditLogs from '@/pages/Audit/AuditLogs'
import AdminPanel from '@/pages/Admin/AdminPanel'
import Analytics from '@/pages/Analytics/Analytics'

// Real-time Components
import { RealTimeNotifications } from '@/components/RealTimeNotifications'

function App() {
  useEffect(() => {
    // Initialize app without authentication
    console.log('NOC Dashboard loaded')
  }, [])

  return (
    <div className="App">
      {/* Real-time notifications overlay */}
      <RealTimeNotifications />
      
      <Routes>
        {/* Main Routes - No authentication required */}
        <Route path="/" element={<Layout />}>
          <Route index element={<Dashboard />} />
          <Route path="dashboard" element={<Dashboard />} />
          <Route path="system-status" element={<SystemStatus />} />
          <Route path="alerts" element={<AlertManagement />} />
          <Route path="technicians" element={<TechnicianWorkload />} />
          <Route path="audit" element={<AuditLogs />} />
          <Route path="analytics" element={<Analytics />} />
          <Route path="admin/*" element={<AdminPanel />} />
        </Route>

        {/* Catch all - redirect to dashboard */}
        <Route path="*" element={<Navigate to="/dashboard" replace />} />
      </Routes>
    </div>
  )
}

export default App
