import React, { useState, useEffect } from 'react'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/Card'
import { Button } from '@/components/ui/Button'
import { 
  Activity, 
  AlertTriangle, 
  CheckCircle, 
  Clock, 
  Users, 
  Monitor,
  Wifi,
  TrendingUp,
  MapPin,
  Smartphone
} from 'lucide-react'
import { useRealTimeAlerts, useRealTimeDevices, useRealTimeTechnicians } from '@/services/socket'

interface DashboardStats {
  totalDevices: number
  onlineDevices: number
  criticalAlerts: number
  activeTechnicians: number
  avgResponseTime: number
  networkUptime: number
}

interface QuickAction {
  id: string
  label: string
  icon: React.ReactNode
  action: () => void
  color: string
}

const EnhancedDashboard: React.FC = () => {
  const [stats, setStats] = useState<DashboardStats>({
    totalDevices: 0,
    onlineDevices: 0,
    criticalAlerts: 0,
    activeTechnicians: 0,
    avgResponseTime: 0,
    networkUptime: 0
  })

  // Real-time data hooks
  const alerts = useRealTimeAlerts()
  const devices = useRealTimeDevices()
  const technicians = useRealTimeTechnicians()

  useEffect(() => {
    // Update stats based on real-time data
    const onlineDevices = devices.filter((device: any) => device.status === 'online').length
    const criticalAlerts = alerts.filter((alert: any) => alert.priority === 'critical' || alert.severity === 'critical').length
    const activeTechnicians = technicians.filter((tech: any) => tech.status === 'available' || tech.status === 'busy').length

    setStats({
      totalDevices: devices.length,
      onlineDevices,
      criticalAlerts,
      activeTechnicians,
      avgResponseTime: Math.floor(Math.random() * 50) + 20, // Mock data
      networkUptime: 99.5
    })
  }, [devices, alerts, technicians])

  const quickActions: QuickAction[] = [
    {
      id: 'create-alert',
      label: 'Create Alert',
      icon: <AlertTriangle className="w-4 h-4" />,
      action: () => console.log('Create alert'),
      color: 'bg-red-500 hover:bg-red-600'
    },
    {
      id: 'view-map',
      label: 'Network Map',
      icon: <MapPin className="w-4 h-4" />,
      action: () => window.location.href = '/map',
      color: 'bg-blue-500 hover:bg-blue-600'
    },
    {
      id: 'mobile-app',
      label: 'Mobile App',
      icon: <Smartphone className="w-4 h-4" />,
      action: () => window.open('https://expo.dev/@noc-project/mobile', '_blank'),
      color: 'bg-green-500 hover:bg-green-600'
    },
    {
      id: 'analytics',
      label: 'Analytics',
      icon: <TrendingUp className="w-4 h-4" />,
      action: () => window.location.href = '/analytics',
      color: 'bg-purple-500 hover:bg-purple-600'
    }
  ]

  const statCards = [
    {
      title: 'Total Devices',
      value: stats.totalDevices.toString(),
      subtitle: `${stats.onlineDevices} online`,
      icon: Monitor,
      color: 'text-blue-600',
      bgColor: 'bg-blue-50 dark:bg-blue-900/20'
    },
    {
      title: 'Critical Alerts',
      value: stats.criticalAlerts.toString(),
      subtitle: 'Requires attention',
      icon: AlertTriangle,
      color: 'text-red-600',
      bgColor: 'bg-red-50 dark:bg-red-900/20'
    },
    {
      title: 'Active Technicians',
      value: stats.activeTechnicians.toString(),
      subtitle: 'On duty now',
      icon: Users,
      color: 'text-green-600',
      bgColor: 'bg-green-50 dark:bg-green-900/20'
    },
    {
      title: 'Avg Response Time',
      value: `${stats.avgResponseTime}ms`,
      subtitle: 'Network latency',
      icon: Clock,
      color: 'text-yellow-600',
      bgColor: 'bg-yellow-50 dark:bg-yellow-900/20'
    },
    {
      title: 'Network Uptime',
      value: `${stats.networkUptime}%`,
      subtitle: 'Last 24 hours',
      icon: Wifi,
      color: 'text-green-600',
      bgColor: 'bg-green-50 dark:bg-green-900/20'
    },
    {
      title: 'System Health',
      value: 'Excellent',
      subtitle: 'All systems operational',
      icon: CheckCircle,
      color: 'text-green-600',
      bgColor: 'bg-green-50 dark:bg-green-900/20'
    }
  ]

  const recentActivities = [
    {
      id: 1,
      type: 'alert',
      message: 'High CPU usage detected on Server-001',
      timestamp: '2 minutes ago',
      severity: 'warning'
    },
    {
      id: 2,
      type: 'resolution',
      message: 'Network connectivity issue resolved',
      timestamp: '15 minutes ago',
      severity: 'success'
    },
    {
      id: 3,
      type: 'maintenance',
      message: 'Scheduled maintenance completed on Router-045',
      timestamp: '1 hour ago',
      severity: 'info'
    },
    {
      id: 4,
      type: 'alert',
      message: 'Disk space running low on Database-003',
      timestamp: '2 hours ago',
      severity: 'warning'
    },
    {
      id: 5,
      type: 'technician',
      message: 'Technician John assigned to critical ticket #1234',
      timestamp: '3 hours ago',
      severity: 'info'
    }
  ]

  const getSeverityColor = (severity: string) => {
    switch (severity) {
      case 'critical':
        return 'text-red-600 bg-red-50 dark:bg-red-900/20'
      case 'warning':
        return 'text-yellow-600 bg-yellow-50 dark:bg-yellow-900/20'
      case 'success':
        return 'text-green-600 bg-green-50 dark:bg-green-900/20'
      default:
        return 'text-blue-600 bg-blue-50 dark:bg-blue-900/20'
    }
  }

  return (
    <div className="space-y-6">
      {/* Header with Quick Actions */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900 dark:text-white">
            NOC Dashboard
          </h1>
          <p className="text-gray-600 dark:text-gray-400 mt-1">
            Real-time network operations center
          </p>
        </div>
        <div className="flex flex-wrap gap-2 mt-4 sm:mt-0">
          {quickActions.map((action) => (
            <Button
              key={action.id}
              onClick={action.action}
              className={`${action.color} text-white`}
              size="sm"
            >
              {action.icon}
              <span className="ml-2">{action.label}</span>
            </Button>
          ))}
        </div>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-6 gap-6">
        {statCards.map((stat, index) => {
          const IconComponent = stat.icon
          return (
            <Card key={index}>
              <CardContent className="p-6">
                <div className="flex items-center">
                  <div className={`rounded-lg p-2 ${stat.bgColor}`}>
                    <IconComponent className={`w-5 h-5 ${stat.color}`} />
                  </div>
                  <div className="ml-4">
                    <p className="text-sm font-medium text-gray-600 dark:text-gray-400">
                      {stat.title}
                    </p>
                    <p className="text-2xl font-bold text-gray-900 dark:text-white">
                      {stat.value}
                    </p>
                    <p className="text-xs text-gray-500 dark:text-gray-500">
                      {stat.subtitle}
                    </p>
                  </div>
                </div>
              </CardContent>
            </Card>
          )
        })}
      </div>

      {/* Main Content Grid */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Recent Activities */}
        <Card className="lg:col-span-2">
          <CardHeader className="flex flex-row items-center justify-between">
            <CardTitle className="flex items-center">
              <Activity className="w-5 h-5 mr-2" />
              Recent Activities
            </CardTitle>
            <Button variant="outline" size="sm">
              View All
            </Button>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {recentActivities.map((activity) => (
                <div key={activity.id} className="flex items-start space-x-3">
                  <div className={`w-2 h-2 rounded-full mt-2 ${getSeverityColor(activity.severity).split(' ')[1]}`} />
                  <div className="flex-1 min-w-0">
                    <p className="text-sm text-gray-900 dark:text-white">
                      {activity.message}
                    </p>
                    <p className="text-xs text-gray-500 dark:text-gray-500">
                      {activity.timestamp}
                    </p>
                  </div>
                  <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${getSeverityColor(activity.severity)}`}>
                    {activity.severity}
                  </span>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>

        {/* System Status */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center">
              <CheckCircle className="w-5 h-5 mr-2" />
              System Status
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div className="flex items-center justify-between">
                <span className="text-sm font-medium">Web Services</span>
                <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium text-green-600 bg-green-50 dark:bg-green-900/20">
                  Operational
                </span>
              </div>
              <div className="flex items-center justify-between">
                <span className="text-sm font-medium">Database</span>
                <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium text-green-600 bg-green-50 dark:bg-green-900/20">
                  Operational
                </span>
              </div>
              <div className="flex items-center justify-between">
                <span className="text-sm font-medium">Real-time Service</span>
                <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium text-green-600 bg-green-50 dark:bg-green-900/20">
                  Connected
                </span>
              </div>
              <div className="flex items-center justify-between">
                <span className="text-sm font-medium">Mobile App</span>
                <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium text-green-600 bg-green-50 dark:bg-green-900/20">
                  Available
                </span>
              </div>
              <div className="flex items-center justify-between">
                <span className="text-sm font-medium">Analytics</span>
                <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium text-green-600 bg-green-50 dark:bg-green-900/20">
                  Processing
                </span>
              </div>
              <div className="flex items-center justify-between">
                <span className="text-sm font-medium">Network Map</span>
                <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium text-green-600 bg-green-50 dark:bg-green-900/20">
                  Interactive
                </span>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Feature Highlights */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <Card className="border-l-4 border-l-blue-500">
          <CardContent className="p-6">
            <div className="flex items-center">
              <Activity className="w-8 h-8 text-blue-600" />
              <div className="ml-4">
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
                  Real-time Monitoring
                </h3>
                <p className="text-sm text-gray-600 dark:text-gray-400">
                  Live updates and notifications for all network events
                </p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="border-l-4 border-l-green-500">
          <CardContent className="p-6">
            <div className="flex items-center">
              <Smartphone className="w-8 h-8 text-green-600" />
              <div className="ml-4">
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
                  Mobile Ready
                </h3>
                <p className="text-sm text-gray-600 dark:text-gray-400">
                  Field technician app for on-the-go management
                </p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="border-l-4 border-l-purple-500">
          <CardContent className="p-6">
            <div className="flex items-center">
              <TrendingUp className="w-8 h-8 text-purple-600" />
              <div className="ml-4">
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
                  Advanced Analytics
                </h3>
                <p className="text-sm text-gray-600 dark:text-gray-400">
                  Comprehensive insights and performance metrics
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}

export default EnhancedDashboard
