import { io, Socket } from 'socket.io-client'
import { useEffect, useState } from 'react'

interface AlertEvent {
  id: string
  type: 'critical' | 'warning' | 'info'
  title: string
  message: string
  timestamp: string
  device?: string
  location?: string
}

interface DeviceStatus {
  id: string
  name: string
  status: 'online' | 'offline' | 'warning'
  location: string
  lastSeen: string
  metrics: {
    uptime: number
    latency: number
    cpuUsage: number
    memoryUsage: number
  }
}

interface TechnicianUpdate {
  id: string
  name: string
  status: 'available' | 'busy' | 'offline'
  location: {
    lat: number
    lng: number
    address: string
  }
  activeTickets: number
  lastUpdate: string
}

interface SystemStatus {
  totalDevices: number
  onlineDevices: number
  activeAlerts: number
  activeTechnicians: number
}

class SocketService {
  private socket: Socket | null = null
  private listeners: Map<string, Function[]> = new Map()

  connect() {
    if (this.socket?.connected) return

    this.socket = io((import.meta as any).env?.VITE_API_URL || 'http://localhost:5000', {
      transports: ['websocket', 'polling'],
      autoConnect: true,
    })

    this.socket.on('connect', () => {
      console.log(' Connected to real-time server')
    })

    this.socket.on('disconnect', () => {
      console.log(' Disconnected from real-time server')
    })

    this.socket.on('connect_error', (error) => {
      console.error(' Connection error:', error)
    })

    // Set up event listeners
    this.setupEventListeners()
  }

  disconnect() {
    if (this.socket) {
      this.socket.disconnect()
      this.socket = null
    }
  }

  private setupEventListeners() {
    if (!this.socket) return

    // Alert events
    this.socket.on('alert:new', (alert: AlertEvent) => {
      this.emit('alert:new', alert)
    })

    // Device events
    this.socket.on('device:updated', (device: DeviceStatus) => {
      this.emit('device:updated', device)
    })

    // Technician events
    this.socket.on('technician:updated', (technician: TechnicianUpdate) => {
      this.emit('technician:updated', technician)
    })

    // System status events
    this.socket.on('system:status', (status: SystemStatus) => {
      this.emit('system:status', status)
    })
  }

  // Subscribe to specific channels
  subscribeToAlerts() {
    this.socket?.emit('subscribe:alerts')
  }

  subscribeToDevices() {
    this.socket?.emit('subscribe:devices')
  }

  subscribeToTechnicians() {
    this.socket?.emit('subscribe:technicians')
  }

  // Send technician location update
  updateTechnicianLocation(data: {
    technicianId: string
    name: string
    status: string
    location: { lat: number; lng: number; address: string }
    activeTickets: number
  }) {
    this.socket?.emit('technician:location', data)
  }

  // Event listener management
  on(event: string, callback: Function) {
    if (!this.listeners.has(event)) {
      this.listeners.set(event, [])
    }
    this.listeners.get(event)?.push(callback)
  }

  off(event: string, callback: Function) {
    const eventListeners = this.listeners.get(event)
    if (eventListeners) {
      const index = eventListeners.indexOf(callback)
      if (index > -1) {
        eventListeners.splice(index, 1)
      }
    }
  }

  private emit(event: string, data: any) {
    const eventListeners = this.listeners.get(event)
    if (eventListeners) {
      eventListeners.forEach(callback => callback(data))
    }
  }

  isConnected(): boolean {
    return this.socket?.connected || false
  }
}

export const socketService = new SocketService()

// React hook for using socket events
export const useSocket = () => {
  const [isConnected, setIsConnected] = useState(false)

  useEffect(() => {
    socketService.connect()

    const checkConnection = () => {
      setIsConnected(socketService.isConnected())
    }

    const interval = setInterval(checkConnection, 1000)
    checkConnection()

    return () => {
      clearInterval(interval)
      socketService.disconnect()
    }
  }, [])

  return { isConnected, socketService }
}

// Hook for subscribing to real-time alerts
export const useRealTimeAlerts = () => {
  const [alerts, setAlerts] = useState<AlertEvent[]>([])
  const { socketService } = useSocket()

  useEffect(() => {
    socketService.subscribeToAlerts()

    const handleNewAlert = (alert: AlertEvent) => {
      setAlerts(prev => [alert, ...prev.slice(0, 49)]) // Keep last 50 alerts
    }

    socketService.on('alert:new', handleNewAlert)

    return () => {
      socketService.off('alert:new', handleNewAlert)
    }
  }, [socketService])

  return alerts
}

// Hook for subscribing to real-time device updates
export const useRealTimeDevices = () => {
  const [devices, setDevices] = useState<Map<string, DeviceStatus>>(new Map())
  const { socketService } = useSocket()

  useEffect(() => {
    socketService.subscribeToDevices()

    const handleDeviceUpdate = (device: DeviceStatus) => {
      setDevices(prev => new Map(prev.set(device.name, device)))
    }

    socketService.on('device:updated', handleDeviceUpdate)

    return () => {
      socketService.off('device:updated', handleDeviceUpdate)
    }
  }, [socketService])

  return Array.from(devices.values())
}

// Hook for subscribing to real-time technician updates
export const useRealTimeTechnicians = () => {
  const [technicians, setTechnicians] = useState<Map<string, TechnicianUpdate>>(new Map())
  const { socketService } = useSocket()

  useEffect(() => {
    socketService.subscribeToTechnicians()

    const handleTechnicianUpdate = (technician: TechnicianUpdate) => {
      setTechnicians(prev => new Map(prev.set(technician.id, technician)))
    }

    socketService.on('technician:updated', handleTechnicianUpdate)

    return () => {
      socketService.off('technician:updated', handleTechnicianUpdate)
    }
  }, [socketService])

  return Array.from(technicians.values())
}

// Hook for system status updates
export const useSystemStatus = () => {
  const [status, setStatus] = useState<SystemStatus | null>(null)
  const { socketService } = useSocket()

  useEffect(() => {
    const handleStatusUpdate = (newStatus: SystemStatus) => {
      setStatus(newStatus)
    }

    socketService.on('system:status', handleStatusUpdate)

    return () => {
      socketService.off('system:status', handleStatusUpdate)
    }
  }, [socketService])

  return status
}
